import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { TranslateConfigService } from 'src/app/services/translate-config.service';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { UpdatePersonalService } from './updatePersonalDetailservice';
import { TacPopupPage } from '../tac-popup/tac-popup.page';


@Component({
  selector: 'app-update-personal-details',
  templateUrl: './update-personal-details.page.html',
  styleUrls: ['./update-personal-details.page.scss'],
})
export class UpdatePersonalDetailsPage  {
  selectedLanguage:string;
  isShowingLoader = false;
  isSuccessful = false;
  selectOptions: any;
  phoneNumber: number;
  verifyPhoneNumberPage = true;
  TACVarified: boolean;
  maskedPhoneNumber: any;
  notMyNumber = false;
  constructor(
    public utils: UtilService,private config: ConfigProvider,
    private alertCtrl: AlertController, private logger: LoggerServiceProvider,
    public translate: TranslateService,private router: Router,
    private translateConfigService: TranslateConfigService,public fb: FormBuilder,
    public modalController: ModalController, public UpdatePersonalService: UpdatePersonalService, )
     {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
    this.translateConfigService.setLanguage('en');

    this.phoneNumber = this.config.userInfo.contactNumber;
    this.maskedPhoneNumber = this.config.userInfo.maskedContactNumber;

    this.config.currentPage.pageName = "UpdatePersonalDetailPage";
    this.utils.analytics("UpdatePersonalDetailPage");
    this.selectOptions = {
      cssClass: "selectPopover-updateAddress"
    };
  }
  ionViewDidLoad() {
    this.logger.log('ionViewDidLoad UpdatePersonalDetailPage');
  }

  downloadAlterationForm() {
    this.utils.openLink(this.config.alterationRequestFormLink);
  }
  emailCompose() {
    this.utils.emailCompose(this.config.contact_us.emailId);
  }
  emailComposewithSubject(subject) {
    this.utils.emailComposewithSubject(this.config.contact_us.emailId, subject);
  }
  continueToUpdate() {
    this.tacPopupModal();
  }
  updateNumber() {
    this.notMyNumber = true;
  }
  cancel() {
    this.router.navigate(['/service']);
    //this.navCtrl.push(ServicePage);
   }
  //  async tacPopupModal() {
  //   const modal = await this.modalController.create(TacPopupPage,
  //     {}, { enableBackdropDismiss: false }
  //   );
  //   modal.onDidDismiss((data => {
  //     if (data && data.TACVarified) {
  //       this.TACVarified = true
  //       this.verifyPhoneNumberPage = false;
  //     }
  //   })
  //   );
  //   return await modal.present();
  // }
  async tacPopupModal() {
    const modal = await this.modalController.create({
      component: TacPopupPage,
      cssClass: 'custom-modal',
      backdropDismiss: false,
    });
    modal.onDidDismiss().then((data) => {
      console.log(data);
      if (data && data.data.TACVarified) {
        this.TACVarified = true
        this.verifyPhoneNumberPage = false;
      }
    });

    return await modal.present();
  }


}
